button {
    display: flex;
    align-items: center;
    justify-content: start;
    width: 400px;
    height: 50px;
    font-size: 24px;
    background: none;
    font-family: 'Tomorrow', sans-serif;
    border: none;
    border-radius: 6px;
    transition: .2s;
}

button:is(:hover, :active) {
    background-color: #333333;
    padding: 0px;
    transition: .2s;
}

button:is(:hover, :active) .selected{
    opacity: 1;
    margin-right: 25px;
    transition: .2s;
}

.selected {
    opacity: 0;
    margin-left: 10px;
    transition: .3s;
}
